<script>
    import Setup from "./Setup.vue";
    export default {
        name: "SetupPassword",
        props: ["mfaNotSet"],
        data() {
            return {
                error: "",
                loading: false,
                redirectUrl: "",
                output: {
                    password: "",
                    confirmPassword: "",
                },
            };
        },
        components: {
            Setup,
        },
        methods: {
            handleFormSubmit() {
                let data = {
                    redirectUrl: this.redirectUrl,
                    password: this.output.password,
                    confirmPassword: this.output.confirmPassword,
                };
                this.error = "";
                if (data.password == "" && data.confirmPassword == "") {
                    this.error = "Password fields cannot be blank.";
                } else if (data.confirmPassword == "") {
                    this.error = "Confirm Password field cannot be blank.";
                } else if (data.password == "") {
                    this.error = "Password field cannot be blank.";
                } else if (data.password != data.confirmPassword) {
                    this.error = "Password fields do not match.";
                }
                if (this.error != "") return;

                this.loading = true;

                this.$ajax
                    .post("/Setup/Password", data)
                    .then(async (resp) => {
                        if (typeof resp.data != "undefined") {
                            const {Error, ClientRedirect, RedirectUrl: redirectUrl, RedirectPage} = resp.data;
                            if (Error != null) {
                                this.error = Error;
                            } else if (ClientRedirect && redirectUrl.length) {
                                const {authenticated, mfaRequired, otpChannels} = await this.$checkMFACookie();
                                const mfaNotSet = mfaRequired && !authenticated;
                                if (mfaNotSet) {
                                    this.$router.push({
                                        name: "MFA",
                                        params: {
                                            redirectUrl,
                                            otpChannels,
                                        },
                                    });
                                } else {
                                    window.location.href = redirectUrl;
                                }
                            } else if (RedirectPage != null) {
                                var queryString = redirectUrl != null ? {redirectUrl} : {};
                                let params = mfaNotSet ? {mfaNotSet: this.mfaNotSet} : undefined;
                                this.$router.push({
                                    name: redirectUrl,
                                    params,
                                    query: queryString,
                                });
                            }
                        }
                    })
                    .catch(() => {})
                    .then(() => {
                        this.loading = false;
                    });
            },
        },
        mounted() {
            if (typeof this.$route.query != "undefined" && typeof this.$route.query.redirectUrl != "undefined") {
                this.redirectUrl = this.$route.query.redirectUrl;
            }
        },
    };
</script>

<template>
    <setup title="Setup Password" :error="error">
        <form method="post" class="card-body" @submit.prevent="handleFormSubmit">
            <passwordPage :loading="loading" v-model="output"></passwordPage>
        </form>
    </setup>
</template>
